html {
  width: 100vw;
  height: 100vh;
}
body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#root {
  height: 100%;
  width: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
}
.app {
  width: 100%;
  height: 100%;
	margin: 0;
  font-family: 'Roboto Condensed', sans-serif;
}
    .grid {
      width: 100%;
      height: 100%;
      display: grid;
      grid-template-rows: repeat(3, 210px 60px auto);
      /* grid-template-areas:  */
     
    } 



.header {
  display: grid;
  grid-template-columns: 200px auto 40px 60px;
  background-color: lightgrey;
}
  .header-items {
    display: grid;
    grid-template-columns: auto auto;
    max-height: 40px;
  }
    .flag {
      align-self: center;
    }
    .login {
      margin: 10px;
      padding-right: 10px;
      text-decoration: underline;
      color: darkblue;
    }

  
.title {
  font-size: 50px;
  padding-left: 350px;
  align-self: center;
  /* text-align: center; */
  color: darkblue;
}
.logo {
  margin: 30px
}
.menu {
  margin: 10px 15px;
  display: grid;
  grid-template-columns: repeat(8, auto);
  position: sticky;
  position: -webkit-sticky;
  top: -1px;
  background-color: #fff;
  z-index: 9999;
}
  .icon {
    align-self: center;
    justify-self: right;
  }

  
.menu--item {
  color: darkblue;
  font-size: 20px;
  align-self: baseline;
  display: grid;
  grid-template-columns: auto auto;

}
.menu--item:hover {
    text-decoration: underline;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    cursor: pointer;
    font-weight: 550;

  }

  .icon {
    align-self: center;
    justify-self: right;
    margin-right: 3px;
  }
.content--grid {
  display: grid;
  width: 100%;
  height: 100vh;
  grid-template-columns: 300px auto 300px;
  grid-template-rows: 100vh;
  overflow-y: scroll;
  
}
  .content--left,
  .content--main,
  .content--right {
    height: 100vh;
  }
    .news {
      box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
      box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
      margin: 2em 1.7em 2em 1.7em;
      width: 270px;
      height: 40vh;
      font-weight: 550;
    }
    h3 {
      padding-top: 8px;
    }
    h3, h4 {
      margin: 12px;
      text-align: justify;
      line-height: 20px;

    }
    h4 {
      font-weight: normal;
    }
    .content--main {
      box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
      box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
      margin: 2rem;
    }
      h1 {
        padding: 20px 0 10px 20px;
      }
      p {
        padding: 0 20px;
        text-align: justify;
        line-height: 26px;

        
      }
    
      .content--right {
        display: grid;
        grid-template-rows: 35vh 5vh 5vh;
      } 
        .videos {
          margin-top: 35px;
          
        }
        
        .canal-youtube--text {
          margin-top: 15px;
          max-height: 50px;
        }
        .canal-youtube {
          display: grid;
          grid-template-columns: 60px 220px;
          max-height: 50px;
          align-self: top;
        }

.icon--youtube {
  margin-right: 10px;
}
.icon--youtube:hover {
  cursor: pointer;
}
.footer{
  background-color: lightgray;
  width: 100%;
  height: 70px;
  /* position: fixed; */
  bottom: 0;
}
  .contato {
    float: right;
    margin: 0 15px 0 0;
    font-size: 14px;
  }
    .property{
      display: flex;
      height: 100%;
      font-size: 12px;
      letter-spacing: 0.5px;
      margin: 0;
      line-height: 15px;
      margin: 0 0 20px 10px;
    }
      .property--rights {
        display: inline-block;
        align-self: flex-end;
      }
